html {
  text-align: center;
   background-color: black;
  color: white;
  background-size: cover;
}
.array{
  margin: 1cm auto;
  height: 1.5cm;
  justify-content: center;
  border-bottom: black double;
  width: 15cm;
  border-radius: 10px;
  padding: 10px 0;
}
.array:hover{
  animation: change-color 1s forwards;
}
@keyframes change-color{
  to{
    background: antiquewhite;
    color: black;
  }
}
.head{
  font-size: 60px;
  font-weight: 500;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-shadow: 2px 4px gray;
}
.search{
  height: 1cm;
  width: 6cm;
  border-radius: 5px 10px 5px 10px;
  box-shadow: 3px 6px gray;
}
.pics{
  height: 50px;
  width: 50px;
  float: left;
}
.name{
  margin-right: 1cm;
}
.symbol{
  margin-left: 1cm;
  margin-right: 1cm;
}
.price{
  margin-left: 1cm;
}
.percentage{
  float: right;
}
.gain{
  color: green;
}
.loss{
  color: red;
}